import React, { Component } from 'react';
import { connect } from 'react-redux';
import config from '../utils/config';
import Dropdown from './Dropdown';
import BetboxBalance from './bet/BetboxBalance';
import Logo from '../struggle.svg';


const mapStateToProps = state => ({
    world: state.world
});

// eslint-disable //

class Navbar extends Component {
    render() {
        let { world } = this.props;
        return (
        <section className="navbar">
            <div className="navbar_side">
                { world.user ? <BetboxBalance balance={world.user.balance} /> : '' }
            </div>
            <div className="navbar_center">
                <a href="/">
                     <img src={Logo} alt="struggle"/>
                </a>
            </div>
            <div className="navbar_side">
                 { world.user ? <Dropdown  /> : <div> <a href={config.mp_browser_uri + '/login/?app_id=' + config.app_id }> LOGIN </a> or <a href={config.mp_browser_uri + '/register/'}> REGISTER </a></div> }
            </div>
        </section>
    );
  }
}

export default connect(mapStateToProps)(Navbar);


