import React from 'react';
import btc from '../../icons/icon_btc.svg';

const BetboxBalance = ({balance}) => {
    return (
        <section className="balance">
            <img src={btc} alt="btc" />
            <span> { balance / 10 + ' ' } bits </span>
        </section>
    );
}

export default BetboxBalance;


